<template>
  <div>
    <!-- 设置返回按钮 START-->
    <headerNavBack :centerTitle="centerTitle"></headerNavBack>
    <!-- 设置返回按钮 END -->
    <section>
      <!-- 系统通知 -->
      <div class="system_notice_item">{{newsInfo.content}}</div>
    </section>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  import { createNamespacedHelpers } from "vuex";
  const { mapActions, mapState } = createNamespacedHelpers(
    "newsModule"
  );
  export default {
    components: {
      headerNavBack,
    },
    data() {
      return {
        // 顶部提示文字
        centerTitle: '通知消息',
        // 消息信息
        newsInfo: Object
      }
    },
    methods: {
      ...mapActions([
        'GET_NEWS_MSG'
      ]),
      // 得到消息信息详情
      getNewsInfomation(){
        this.GET_NEWS_MSG({
          params: {
            sysmsgId: this.$route.params.id
          }
        }).then(data=>{
          this.newsInfo = data.data.result;
        })
      }
    },
    created() {
      this.getNewsInfomation()
    }
  }
</script>
<style lang="less" scoped>
  /* 系统通知 START */
  .system_notice_item {
    min-height: 3.125rem;
    padding: .625rem 0;
    background-color: #fff;
    text-indent: 2em;
  }
</style>



